import React from 'react';
import { Container, Typography, Grid, Paper, SvgIcon } from '@mui/material';
import { motion } from 'framer-motion';
import CodeIcon from '@mui/icons-material/Code';
import StorageIcon from '@mui/icons-material/Storage';
import PhoneAndroidIcon from '@mui/icons-material/PhoneAndroid';
import CloudIcon from '@mui/icons-material/Cloud';
import DatabaseIcon from '@mui/icons-material/Storage';
import BuildIcon from '@mui/icons-material/Build';

interface SkillCategory {
  title: string;
  icon: typeof SvgIcon;
  description: string;
  skills: {
    name: string;
    level: number;
    icon: string;
  }[];
}

const skillCategories: SkillCategory[] = [
  {
    title: "Frontend Development",
    icon: CodeIcon,
    description: "Creating responsive and interactive user interfaces",
    skills: [
      { name: "React", level: 90, icon: "/logos/react.svg" },
      { name: "TypeScript", level: 85, icon: "/logos/typescript.svg" },
      { name: "JavaScript", level: 90, icon: "/logos/javascript.svg" },
      { name: "HTML/CSS", level: 85, icon: "/logos/html5.svg" }
    ]
  },
  {
    title: "Backend Development",
    icon: StorageIcon,
    description: "Building robust and scalable server-side applications",
    skills: [
      { name: "Node.js", level: 85, icon: "/logos/nodejs.svg" },
      { name: "Express", level: 80, icon: "/logos/express.svg" },
      { name: "Python", level: 75, icon: "/logos/python.svg" },
      { name: "Java", level: 70, icon: "/logos/java.svg" }
    ]
  },
  {
    title: "Mobile Development",
    icon: PhoneAndroidIcon,
    description: "Developing cross-platform mobile applications",
    skills: [
      { name: "React Native", level: 85, icon: "/logos/react.svg" },
      { name: "Flutter", level: 70, icon: "/logos/flutter.svg" },
      { name: "Android", level: 65, icon: "/logos/android.svg" },
      { name: "iOS", level: 60, icon: "/logos/apple.svg" }
    ]
  },
  {
    title: "Cloud & DevOps",
    icon: CloudIcon,
    description: "Managing cloud infrastructure and deployment",
    skills: [
      { name: "AWS", level: 75, icon: "/logos/aws.svg" },
      { name: "Docker", level: 80, icon: "/logos/docker.svg" },
      { name: "Kubernetes", level: 70, icon: "/logos/kubernetes.svg" },
      { name: "CI/CD", level: 75, icon: "/logos/jenkins.svg" }
    ]
  },
  {
    title: "Databases",
    icon: DatabaseIcon,
    description: "Working with various database systems",
    skills: [
      { name: "MongoDB", level: 85, icon: "/logos/mongodb.svg" },
      { name: "PostgreSQL", level: 80, icon: "/logos/postgresql.svg" },
      { name: "MySQL", level: 75, icon: "/logos/mysql.svg" },
      { name: "Redis", level: 70, icon: "/logos/redis.svg" }
    ]
  },
  {
    title: "Tools & Others",
    icon: BuildIcon,
    description: "Using various development tools and technologies",
    skills: [
      { name: "Git", level: 90, icon: "/logos/git.svg" },
      { name: "VS Code", level: 85, icon: "/logos/vscode.svg" },
      { name: "Webpack", level: 75, icon: "/logos/webpack.svg" },
      { name: "Jest", level: 80, icon: "/logos/jest.svg" }
    ]
  }
];

const Skills: React.FC = () => {
  return (
    <Container maxWidth="lg" className="py-20" id="skills">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        whileInView={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        viewport={{ once: true }}
      >
        <Typography
          variant="h2"
          className="text-3xl md:text-4xl font-bold text-center mb-12"
        >
          My Skills
        </Typography>

        <Grid container spacing={4}>
          {skillCategories.map((category, index) => {
            const Icon = category.icon;
            return (
              <Grid item xs={12} md={6} lg={4} key={category.title}>
                <motion.div
                  initial={{ opacity: 0, y: 20 }}
                  whileInView={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: index * 0.1 }}
                  viewport={{ once: true }}
                >
                  <Paper className="p-6 h-full">
                    <div className="flex items-center mb-4">
                      <Icon sx={{ color: 'primary.main', marginRight: 1 }} />
                      <Typography variant="h6" className="font-semibold">
                        {category.title}
                      </Typography>
                    </div>
                    <Typography className="text-gray-600 dark:text-gray-300 mb-6">
                      {category.description}
                    </Typography>
                    <div className="space-y-4">
                      {category.skills.map((skill) => (
                        <div key={skill.name}>
                          <div className="flex justify-between mb-1">
                            <Typography className="font-medium">
                              {skill.name}
                            </Typography>
                            <Typography className="text-gray-600 dark:text-gray-400">
                              {skill.level}%
                            </Typography>
                          </div>
                          <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                            <motion.div
                              className="bg-primary-600 h-2 rounded-full"
                              initial={{ width: 0 }}
                              whileInView={{ width: `${skill.level}%` }}
                              transition={{ duration: 1, delay: 0.5 }}
                              viewport={{ once: true }}
                            />
                          </div>
                        </div>
                      ))}
                    </div>
                  </Paper>
                </motion.div>
              </Grid>
            );
          })}
        </Grid>
      </motion.div>
    </Container>
  );
};

export default Skills; 